import React, { useState, useRef, useEffect, useCallback } from "react";
import "./App.css";
import MyCropper from "./Crop";
import Button from "@mui/joy/Button";
import Input from "@mui/joy/Input";
import Table from "@mui/joy/Table";
import { styled } from "@mui/material/styles";
// import { Dayjs } from "dayjs";
import { DemoContainer } from "@mui/x-date-pickers/internals/demo";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";

import OpenInNew from "@mui/icons-material/OpenInNew";

const initialText = ["", "", "", ""];
const fixedText = ["공사명", "공종", "위치", "내용", "일자"];

const App = () => {
    const [imageName, setImageName] = useState("");
    const [imageSrc, setImageSrc] = useState("");
    const [variant, setVariant] = React.useState("soft");
    const [inputText, setInputText] = useState(initialText);
    const [textList, setTextList] = useState([]);
    const [isFileSelected, setIsFileSelected] = useState(false);
    const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
    // const canvasRef = useRef(null);
    const fileInputRef = useRef(null);
    const [value, setValue] = React.useState(null);
    const selectedDate = value ? value.format("YYYY-MM-DD") : "";

    const canvasContainer = document.createElement("div");
    useEffect(() => {
        const newTextList = inputText.map((text, index) =>
            text.trim() !== "" ? text : ""
        );
        setTextList(newTextList);
    }, [inputText]);

    const handleInputChange = (index, e) => {
        const newText = [...inputText];
        newText[index] = e.target.value;
        setInputText(newText);
    };

    const handleAddText = () => setInputText(initialText);

    const handleFileChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setImageName(file.name);
            const reader = new FileReader();
            reader.onload = () => setImageSrc(reader.result);
            reader.readAsDataURL(file);
            setIsFileSelected(true);
        } else {
            console.error("No file selected.");
        }
    };

    const handleFileButtonClick = () => fileInputRef.current.click();
    const handleCropComplete = useCallback((croppedAreaPixels) => {
        setCroppedAreaPixels(croppedAreaPixels); // Set croppedAreaPixels
    }, []);
    const handleDownloadImage = async () => {
        console.log("Cropped Area Pixels:", croppedAreaPixels);
        if (!imageSrc || !croppedAreaPixels) {
            console.error("Image source or cropped area pixels not available.");
            return;
        }

        const canvasContainer = document.createElement("div");
        canvasContainer.style.display = "flex";
        canvasContainer.style.flexDirection = "column";
        canvasContainer.style.border = "1px solid red";

        const { width, height, x, y } = croppedAreaPixels;

        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext("2d");
        const MAX_SIZE = 500;
        let newWidth = width;
        let newHeight = height;
        if (width > MAX_SIZE || height > MAX_SIZE) {
            if (width > height) {
                newWidth = MAX_SIZE;
                newHeight = (height / width) * MAX_SIZE;
            } else {
                newHeight = MAX_SIZE;
                newWidth = (width / height) * MAX_SIZE;
            }
        }
        const selectedDateText = selectedDate;
        ctx.fillText(selectedDateText, 10, 50);

        canvas.width = newWidth;
        canvas.height = newHeight;

        const image = new Image();
        image.onload = async () => {
            ctx.drawImage(
                image,
                x,
                y,
                width,
                height,
                0,
                0,
                newWidth,
                newHeight
            );

            ctx.fillStyle = "white";
            const paddingTop = 8; // Top padding size
            const paddingBottom = 8; // Bottom padding size

            // Draw a rectangle with top and bottom padding
            ctx.fillStyle = "white";
            ctx.fillRect(
                10,
                canvas.height - 110 - paddingTop,
                180,
                74 + paddingTop + paddingBottom
            );
            ctx.font = "10px Arial";
            ctx.fillStyle = "#222";

            // Draw borders below fixedText
            fixedText.forEach((text, index) => {
                if (index < 4) {
                    // Draw border only for the first four lines
                    const y = canvas.height - 100 + index * 18;
                    const borderWidth = 2; // Width of the border
                    const borderY = y + borderWidth / 2; // Y position of the border
                    ctx.strokeStyle = "#f1f4f8"; // Color of the border
                    ctx.lineWidth = borderWidth; // Width of the border

                    // Draw border
                    ctx.beginPath();
                    ctx.moveTo(10, borderY);
                    ctx.lineTo(190, borderY);
                    ctx.stroke();
                }

                // Draw fixedText
                const y = canvas.height - 105 + index * 18;
                ctx.fillText(text, 15, y);
            });

            // Draw borders below textList
            const userTextX = 60;
            ctx.textAlign = "left";
            textList.forEach((text, index) => {
                if (index < 4) {
                    // Draw border only for the first four lines
                    const y = canvas.height - 100 + index * 18;
                    const borderWidth = 2; // Width of the border
                    const borderY = y + borderWidth / 2; // Y position of the border
                    ctx.strokeStyle = "#eee"; // Color of the border
                    ctx.lineWidth = borderWidth; // Width of the border

                    // Draw border
                    ctx.beginPath();
                    ctx.moveTo(userTextX, borderY);
                    ctx.lineTo(190, borderY);
                    ctx.stroke();
                }

                // Draw textList
                const y = canvas.height - 105 + index * 18;
                if (text.trim() !== "") {
                    ctx.fillText(text, userTextX, y);
                }
            });

            // Draw the selected date text below the last item of textList
            const selectedDateTextY = canvas.height - 105 + 4 * 18; // Calculate Y position for the selected date text
            ctx.fillText(selectedDateText, userTextX, selectedDateTextY); // Draw the selected date text below the last item of textList

            // 이미지 다운로드
            const imageDataURL = canvas
                .toDataURL("image/png")
                .replace("image/png", "image/octet-stream");
            const downloadLink = document.createElement("a");
            const originalFileName = imageName || "cropped_image";
            downloadLink.href = imageDataURL;
            downloadLink.download = `${originalFileName}`;

            // 다운로드 링크 클릭
            downloadLink.click();

            // 이미지 다운로드 후에는 캔버스와 캔버스 컨테이너를 삭제
            setTimeout(() => {
                document.body.removeChild(canvasContainer);
            }, 0);
        };

        image.src = imageSrc;
        canvasContainer.appendChild(image);
        document.body.appendChild(canvasContainer);
    };

    const renderTextInputs = () => {
        return fixedText.map((label, index) => (
            <tr key={index}>
                <td>
                    <p className="label">{label}</p>
                </td>
                <td>
                    {index === fixedText.length - 1 ? ( // 마지막 인덱스인지 확인
                        <LocalizationProvider dateAdapter={AdapterDayjs}>
                            <DemoContainer components={["DatePicker"]}>
                                <DatePicker
                                    value={value}
                                    onChange={(newValue) => setValue(newValue)}
                                    slotProps={{
                                        openPickerIcon: { fontSize: "small" },
                                    }}
                                />
                            </DemoContainer>
                        </LocalizationProvider>
                    ) : (
                        // 다른 인덱스에 대한 텍스트 입력 렌더링
                        <Input
                            size="sm"
                            type="text"
                            variant="outlined"
                            color="primary"
                            placeholder="여기에 텍스트 입력"
                            value={inputText[index]}
                            onChange={(e) => handleInputChange(index, e)}
                        />
                    )}
                </td>
            </tr>
        ));
    };
    const VisuallyHiddenInput = styled("input")({
        clip: "rect(0 0 0 0)",
        clipPath: "inset(50%)",
        height: 1,
        overflow: "hidden",
        position: "absolute",
        bottom: 0,
        left: 0,
        whiteSpace: "nowrap",
        width: 1,
    });
    const renderTextList = () => {
        return fixedText.map((label, index) => (
            <tr key={index}>
                <td className="fixed-text">{label}</td>
                <td className="add-text">{textList[index]}</td>
            </tr>
        ));
    };

    return (
        <div className="container">
            <div className="image-upload">
                <div className="mb10">
                    <Table
                        className="input-table"
                        borderAxis="none"
                        sx={{
                            "--TableCell-paddingY": "0px",
                        }}>
                        <tbody>{renderTextInputs()}</tbody>
                    </Table>
                </div>
                <div>
                    <input
                        type="file"
                        ref={fileInputRef}
                        style={{ display: "none" }}
                        onChange={handleFileChange}
                    />
                    <Button
                        variant={variant}
                        color="primary"
                        onClick={handleFileButtonClick}>
                        Choose File
                    </Button>{" "}
                    <Button
                        className="addBtn"
                        onClick={handleAddText}
                        variant={variant}
                        color="danger"
                        disabled={!isFileSelected}>
                        reset
                    </Button>
                </div>
                <div className="fileContainer">
                    {/* <div className="fileInput">
                        <p id="fileName">{imageName}</p>
                    </div> */}
                    <div className="buttonContainer">
                        <Button
                            component="a"
                            href="#as-link"
                            variant={variant}
                            color="success"
                            onClick={handleDownloadImage}
                            startDecorator={<OpenInNew />}>
                            download
                        </Button>
                    </div>
                </div>
            </div>
            <div className="image-show">
                {/* <canvas ref={canvasRef} style={{ display: "none" }}></canvas> */}
                <div className="image-container">
                    {imageSrc && (
                        <div className="img-container">
                            <MyCropper
                                imageSrc={imageSrc}
                                onCropComplete={handleCropComplete}
                            />

                            <Table
                                className="text-table text-overlay"
                                size="sm"
                                sx={{
                                    "--TableCell-paddingY": "0px",
                                }}>
                                <tbody>
                                    {" "}
                                    {renderTextList().slice(0, -1)}{" "}
                                    {/* 마지막 항목인 "일자"를 제외하고 출력 */}
                                </tbody>
                                <tr>
                                    <td className="fixed-text">일자</td>
                                    <td className="add-text">
                                        {value
                                            ? value.format("YYYY-MM-DD")
                                            : ""}
                                    </td>{" "}
                                    {/* 선택한 날짜 표시 */}
                                </tr>
                            </Table>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default App;
